.wrap {
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wrap .head {
  width: 100%;
  height: 100px;
}
.wrap .middle {
  width: 60%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
.wrap .middle .box {
  width: 28%;
  height: 150px;
  background: #fff;
  border-radius: 10px;
  margin-bottom: 50px;
  box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.03);
}
.wrap .middle .box .up {
  height: 70%;
}
.wrap .middle .box .down {
  margin: 0 auto;
  text-align: center;
}
.wrap .footer {
  width: 100%;
  border: 1px solid blue;
}
